.fabric-preview{padding:60px 0;max-width:1200px;margin:0 auto}.section-title{font-size:2.4rem;font-weight:700;text-align:center;margin-bottom:40px;color:#111}.fabric-grid{display:grid;grid-template-columns:repeat(4, 1fr);gap:15px}@media(max-width: 1200px){.fabric-grid{grid-template-columns:repeat(2, 1fr);gap:20px}}@media(max-width: 576px){.fabric-grid{grid-template-columns:1fr}}.fabric-card{display:flex;flex-direction:column;background-color:#fff;border-radius:8px;overflow:hidden;height:100%;transition:transform .3s ease,box-shadow .3s ease;border:1px solid #eee}.fabric-card:hover{transform:translateY(-5px);box-shadow:0 10px 20px rgba(0,0,0,.05)}.fabric-image{width:100%;height:200px;overflow:hidden}.fabric-image img{width:100%;height:100%;-o-object-fit:cover;object-fit:cover;transition:transform .5s ease}.fabric-image img:hover{transform:scale(1.05)}.fabric-content{display:flex;flex-direction:column;padding:15px;flex:1}.fabric-name{font-size:1.8rem;font-weight:600;margin:0 0 5px;color:#333}.fabric-subtitle{font-size:1.2rem;font-weight:500;color:#3a62d0;margin-bottom:15px;text-transform:uppercase}.fabric-details{display:flex;flex-direction:column;gap:15px;margin-bottom:20px;flex:1}.detail-item{display:flex;flex-direction:column;gap:3px}.detail-label{font-size:1.2rem;font-weight:600;color:#333}.detail-text{font-size:1.2rem;line-height:1.5;color:#555}.detail-composition{font-size:1.2rem;line-height:1.5;color:#555;margin-bottom:10px;border-bottom:1px solid #eee;padding-bottom:10px}.fabric-button{display:block;text-align:center;padding:10px;background-color:rgba(0,0,0,0);border:1px solid #333;border-radius:4px;color:#333;font-size:1.3rem;font-weight:600;text-decoration:none;transition:all .3s ease;margin-top:auto}.fabric-button:hover{background-color:#333;color:#fff}